import React from 'react';
import {
  Container,
  Row,
  Col,
  Card,
  CardLink,
  CardTitle,
  CardColumns,
  CardBody,
  CardImg
} from 'reactstrap';

import Section from './Section'

import logoDiscord from '../images/discord-large.svg';
import './ContactSection.css';

class ContactSection extends React.Component {

  render() {
    return <Section id="contact">
      <Container>
        <Row className="justify-content-center">
          <Col xs="8" sm="4" md="3" className="text-center">
            <h2>Contact</h2>
            <hr/>
          </Col>
        </Row>
        <Row className="justify-content-center mb-3">
          <Col xs="12" sm="6" md="4">
            <a href="https://discordapp.com/invite/8GakFcT/" target="_blank" rel="noopener noreferrer">
              <img src={logoDiscord} alt="Discord Logo"/>
            </a>
          </Col>
        </Row>
        <Row className="justify-content-center">
          <Col xs="8" sm="4" md="3" className="text-center">
            <h2>Contributors</h2>
            <hr/>
          </Col>
        </Row>
        <CardColumns>
          <Card>
            <CardImg top width="100%" src="https://cdn.discordapp.com/avatars/376817315830038530/87dd80c68e0598ea39af4e0472b299b7.png?size=256" alt="Sk1er avatar" />
            <CardBody>
              <CardTitle>Sk1er</CardTitle>
              <CardLink href="#">Sk1er#1030</CardLink>
              <CardLink href="https://github.com/Sk1er">GitHub</CardLink>
              <CardLink href="https://hypixel.net/members/sk1er.199731/">Hypixel</CardLink>
            </CardBody>
          </Card>
          <Card>
            <CardImg top width="100%" src="https://cdn.discordapp.com/avatars/248159137370734601/667cb334d3a85c850d9890e786cbf50c.png?size=256" alt="CoalOres avatar" />
            <CardBody>
              <CardTitle>CoalOres</CardTitle>
              <CardLink href="#">CoalOres#4408</CardLink>
              <CardLink href="https://github.com/CoalCoding">GitHub</CardLink>
              <CardLink href="https://hypixel.net/members/coalores.125632/">Hypixel</CardLink>
            </CardBody>
          </Card>
          <Card>
            <CardImg top width="100%" src="https://cdn.discordapp.com/avatars/247785387919933440/e8f6af129f0d6d4db93d8c7360aac15a.png?size=256" alt="Kevin avatar" />
            <CardBody>
              <CardTitle>Kevin</CardTitle>
              <CardLink href="#">Kevin#1846</CardLink>
              <CardLink href="https://github.com/KevinPriv">GitHub</CardLink>
              <CardLink href="https://hypixel.net/members/simisage.853433/">Hypixel</CardLink>
            </CardBody>
          </Card>
          <Card>
            <CardImg top width="100%" src="https://cdn.discordapp.com/avatars/290921387655430144/1495ae41593665e29f683d63d502c600.png?size=256" alt="Cubxity avatar" />
            <CardBody>
              <CardTitle>Cubxity</CardTitle>
              <CardLink href="#">Cubxity#7801</CardLink>
              <CardLink href="https://github.com/Cubxity">GitHub</CardLink>
              <CardLink href="https://hypixel.net/members/vrcube.1821108/">Hypixel</CardLink>
            </CardBody>
          </Card>
          <Card>
            <CardImg top width="100%" src="https://cdn.discordapp.com/avatars/207440827385905153/5269feb31998b410f45fa2014a06618e.png?size=256" alt="boomboompower avatar" />
            <CardBody>
              <CardTitle>boomboompower</CardTitle>
              <CardLink href="#">boomboompower#6245</CardLink>
              <CardLink href="https://github.com/boomboompower">GitHub</CardLink>
              <CardLink href="https://hypixel.net/members/boomboompower.350634/">Hypixel</CardLink>
            </CardBody>
          </Card>
        </CardColumns>
      </Container>
    </Section>;
  }

}

export default ContactSection;



