import React from 'react';
import {Container, Row, Col, Card, CardTitle, CardText} from 'reactstrap';

import Section from './Section'

import './PrivacyPolicySection.css';

class PrivacyPolicySection extends React.Component {

  render() {
    return <Section id="privacy-policy">
      <Container>
        <Row className="justify-content-center">
          <Col xs="8" sm="4" md="3" className="text-center">
            <h2>Privacy Policy</h2>
            <hr/>
          </Col>
        </Row>
        <Row>
          <Col sm="12" md="6" className="mb-sm-3 mb-md-0">
            <Card body>
              <CardTitle>What data does Hyperium Collect?</CardTitle>
              <CardText>
                When using Hyperium, the client and integrated mods send a few small pieces of information to a remote
                server in exchange for a temporary token for accessing data from Hypixel.
                This information includes your Minecraft UUID and username, Minecraft Version, Client Version and specific mod being used.
                This information is processed and used to determine if the client is out of date or if the client should abort the startup procedure.
              </CardText>
            </Card>
          </Col>
          <Col sm="12" md="6">
            <Card body>
              <CardTitle>What we do with the data</CardTitle>
              <CardText>
                The data is stored securely for analytic purposes.
                We will never sell or release the data collected from specific users.
                All analytic graphs may be viewed on sk1er.club/graphs/hyperium
              </CardText>
            </Card>
          </Col>
        </Row>
      </Container>
    </Section>;
  }

}

export default PrivacyPolicySection;



